@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=IBM+Plex+Mono:wght@300&family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel&family=Patrick+Hand&family=Sacramento&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.profileImg{
    height: 200px;
    width: 200px;
    border-radius: 10rem;
    margin-top: 5rem;
  }
  
  .nameTag{
    font-family: 'Sacramento', cursive;
    font-size: 3rem;
    font-weight: lighter;
    margin: 0;
    margin-top: 20px;
    margin-bottom: 15px;
  }
  
  .desc{
    font-family: 'Cinzel', serif;
    font-size: 2.5rem;
    font-weight: lighter;
    margin: 0;
  }
  
  .midLine{
    background-color: #61dafb;
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
    height: 2px;
    width: 95%;
    border-radius: 10px;
  }
  
  .about{
    font-family: 'Cinzel', serif;
    font-size: 1rem;
    font-weight: lighter;
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
    height: 160px;
    width: 95%;
    text-align: justify;
  }

  .footer{
    margin-bottom: 0;
    margin-top: 75px;
    height: 50px;
  }

  .footerText{
    font-family: 'Dancing Script', cursive;
  }

  .copyright{
    font-family: 'Playfair Display', serif;
    font-size: smaller;
    margin-top: 10px;
  }
  
.container{
    font-family: 'Cinzel', cursive;
    font-size: 2.5rem;
    height: 150px;
    width: 100%;
    margin-top: 175px;
}
.article{
    height: 200px;
    width: 80%;
    margin: auto;
    background-color: rgb(242, 242, 242);
    border-radius: 15px;
    padding: 15px;
    text-align: left;
    padding-left: 40px;
}

.contianer{
    margin: 0;
    margin-top: 75px;
}

.read{
    height: 25px;
    width: 3rem;
    margin-left: 5px;
}

.read:hover{
    cursor: pointer;
}

.text1{
    margin-bottom: 45px;
}

.text2{
    margin-bottom: 10px;
}

.skillSec{
    display: grid;
    grid-template-columns: 30% 30% 30%;
    justify-content: center;
    margin-top: 100px;
}

.skillName{
    font-family: 'IBM Plex Mono', monospace;
    background-color: rgb(242, 242, 242);
    height: 70px;
    border-radius: 10px;
    line-height: 30px;
    margin: 5px;
}
.headMessage{
    height: 100px;
    width: 100%;
    line-height: 100px;
    margin-top: 75px;
    font-size: 2.5rem;
    font-family: 'Cinzel', serif;
}

.handles{
    height: 400px;
    width: 100%;
}

.socials{
    height: 100px;
    width: 100px;
}

.socials:hover{
    cursor: pointer;
}

.navBar{
    font-family: 'Playfair Display', serif;
    margin-top: 10px;
}

.navItem{
    margin: 1.5rem;
    font-size: 1.2rem;
}

.navItem:hover{
    cursor: pointer;
    color: orangered;
}
